@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Luckiest+Guy&family=Monoton&family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
.avatar {
  width: 100%;
  height: 100%;
  display: flex; 
  justify-content: center;
  align-items: center;
}

.avatar img {
  display: block;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card {
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  padding: 1rem;
  overflow: hidden;
  background: white;
}

.user-item {
  margin: 1rem;
  width: calc(45% - 2rem);
  min-width: 17.5rem;
}

.user-item a {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  padding: 1rem;
  color: white;
  background: #292929;
}

.user-item a:hover,
.user-item a:active {
  background: #ffd900;
}

.card.user-item__content {
  padding: 0 !important;
}

.user-item__image {
  width: 4rem;
  height: 4rem;
  margin-right: 1rem;
}

.user-item__info h2 {
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  font-weight: normal;
  color: #ffd900;
}

.user-item:hover h2,
.user-item:active h2,
.user-item:hover h3,
.user-item:active h3 {
  color: #292929;
}

.user-item__info h3 {
  margin: 0;
}

.users-list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 90%;
  max-width: 50rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.desc {
  text-align: center;
  font-family: 'Lobster';
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
  color: white;
  text-shadow: 2px 2px black;
}

@media (min-width: 768px) {
  .desc {
    display: flex;
    justify-content: center;
    align-content: center;
    overflow: auto;
  }
}

.credentials {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

@media (min-width: 768px) {
  .credentials {
    display: flex;
    justify-content: center;
    align-content: center;
    overflow: auto;
  }
}

